  image: '/img/logos/mega-finance.png',
  profile: `
  Mega Finance merupakan perusahaan yang bernaung di bawah naungan CT Corpora, seperti halnya dengan Bank Mega, Trans TV, Trans7 , dan lain-lain. CT Corpora adalah perusahaan holding yang berbasis di Indonesia memegang perusahaan yang aktif di beberapa industri. Kelompok perusahaan ini dibagi menjadi tiga bisnis utama, yaitu Bidang Keuangan dan  Pembiayaan, Media, Gaya Hidup & Hiburan, serta Sumber Daya Alam.<br><br>

  Kantor pusat PT Mega Finance berlokasi di Graha Mega Finance, JI .Wijaya No. I No- 19 Kebayoran Baru, Jakarta Selatan.  PT. Mega Finance berdiri pada tanggal 15 Maret 1995, dengan nama Para Finance. Awalnya, Mega Finance lebih banyak bergerak di bidang leasing (sewa guna usaha), factoring (anjak piutang), dan consumer finance (pembiayaan konsumen) dengan sasarannya adalah konsumen korporasi dan jenis kendaraan bermotor.<br><br>

  Pada bulan Oktober 2000 Para Finance mengalihkan fokusnya ke bidang consumer, finance dengan sasarannya adalah konsumen individu dan organisasi serta jenis produk kendaraan roda dua (motor) yang khusus diproduksi Jepang dan Italia. yaitu Honda, Yamaha, Suzuki, Kawasaki dan Vespa.<br><br>

  Pada Juni 2010, Chairul Tanjung (Chairman) mengeluarkan kebijakan untuk mengganti nama Para Finance menjadi Mega Finance. Hal ini dilakukan untuk membedakan divisi bidng usaha Para Group.<br><br>

  Bisnis Mega Finance semakin berkembang dengan hadirnya pembiayaan elektronik (Mega. Zip) dan pembiayaan multiguna. Mega Zip hadir di kota-kota besar, khususnya di Transmart Carrefour. Hypermart, Electronic Solution dan Modern Channel lainnya, sehingga semakin dekat dengan konsumen di seluruh Indonesia. Sedangkan pembiayaan multiguna hadir untuk membantu konsumen yang membutuhkan biaya untuk pengobatan, pendidikan, dan lain-lain dengan proses yang cepat.
  Pada era digital ini PT. Mega Finance mengeluarkan produk digitalnya dengan nama Blicicil. Blicicil merupakan produk digital dari Mega Finance, sebagai solusi pembiayaan yang aman dan terpercaya untuk segala kebutuhan, mulai dari pembiayaan motor baru, peralatan elektronik, gadget, rumah tangga, Pinjaman Multiguna, hingga modal instan.<br><br>


  `,
